"use client";

import { Separator } from "@trading-platform/ui/separator";

import { EarlyAccessSection } from "~/components/landing/early-access-section";
import { FAQSection } from "~/components/landing/faq-section";
import { FeaturesSection } from "~/components/landing/features-section";
import { FinalCTASection } from "~/components/landing/final-cta-section";
import { Footer } from "~/components/landing/footer";
import { HeroSection } from "~/components/landing/hero-section";
import { Navigation } from "~/components/landing/navigation";
import { POSIntegrationSection } from "~/components/landing/pos-integration-section";
import { PricingSection } from "~/components/landing/pricing-section";
import { PrivacySection } from "~/components/landing/privacy-section";
import { ProblemSection } from "~/components/landing/problem-section";
import { ReservationDialog } from "~/components/landing/reservation-dialog";
import {
  ReservationDialogProvider,
  useReservationDialog,
} from "~/components/landing/reservation-dialog-context";
import { SolutionSection } from "~/components/landing/solution-section";
import { WhatHappensNextSection } from "~/components/landing/what-happens-next-section";

function HomePageContent() {
  const { isOpen, closeDialog, openDialog } = useReservationDialog();

  const handleOpenChange = (open: boolean) => {
    console.log("handleOpenChange called with:", open);
    if (open) {
      openDialog();
    } else {
      closeDialog();
    }
  };

  console.log("HomePageContent render - isOpen:", isOpen);

  return (
    <>
      <div className="relative min-h-screen bg-[#0c0c0c]">
        <Navigation />

        <main className="pt-20 sm:pt-24 md:pt-[112px]">
          <HeroSection />
          <Separator className="bg-gradient-to-r from-transparent via-white/50 to-transparent" />

          <ProblemSection />
          <Separator className="bg-gradient-to-r from-transparent via-white/30 to-transparent" />

          <SolutionSection />
          <Separator className="bg-gradient-to-r from-transparent via-white/30 to-transparent" />

          <FeaturesSection />
          <Separator className="bg-gradient-to-r from-transparent via-white/30 to-transparent" />

          <POSIntegrationSection />
          <Separator className="bg-gradient-to-r from-transparent via-white/30 to-transparent" />

          <PricingSection />
          <Separator className="bg-gradient-to-r from-transparent via-white/30 to-transparent" />

          <EarlyAccessSection />
          <Separator className="bg-gradient-to-r from-transparent via-white/30 to-transparent" />

          <WhatHappensNextSection />
          <Separator className="bg-gradient-to-r from-transparent via-white/30 to-transparent" />

          <FAQSection />
          <Separator className="bg-gradient-to-r from-transparent via-white/30 to-transparent" />

          <PrivacySection />
          <Separator className="bg-gradient-to-r from-transparent via-white/30 to-transparent" />

          <FinalCTASection />
          <Footer />
        </main>
      </div>
      <ReservationDialog open={isOpen} onOpenChange={handleOpenChange} />
    </>
  );
}

export default function HomePage() {
  return (
    <ReservationDialogProvider>
      <HomePageContent />
    </ReservationDialogProvider>
  );
}
