"use client";

import { createContext, useContext, useState, type ReactNode } from "react";

interface ReservationDialogContextType {
  isOpen: boolean;
  openDialog: () => void;
  closeDialog: () => void;
}

const ReservationDialogContext = createContext<
  ReservationDialogContextType | undefined
>(undefined);

export function ReservationDialogProvider({
  children,
}: {
  children: ReactNode;
}) {
  const [isOpen, setIsOpen] = useState(false);

  const openDialog = () => {
    setIsOpen(true);
  };
  const closeDialog = () => {
    setIsOpen(false);
  };

  return (
    <ReservationDialogContext.Provider
      value={{ isOpen, openDialog, closeDialog }}
    >
      {children}
    </ReservationDialogContext.Provider>
  );
}

export function useReservationDialog() {
  const context = useContext(ReservationDialogContext);
  if (!context) {
    throw new Error(
      "useReservationDialog must be used within ReservationDialogProvider",
    );
  }
  return context;
}
